class Permissions {
  async getMedia() {
    let stream = null;
    const constraints = {
      audio: true,
    };
    try {
      stream = await navigator.mediaDevices.getUserMedia(constraints);
      return { stream: stream, msg: "", status: "ready" };
    } catch (err) {
      return { error: err, msg: "Please provide permissions to make call", status: "permission_pending" };
    }
  }
}

export default new Permissions();
