<template>
  <div class="width-400">
    <b-card body-class="p-4">
      <b-form ref="form" @submit.prevent="onSubmit">
        <b-form-group id="input-group-1" label="Enter Name" label-for="input-1">
          <b-form-input type="text" v-model="form.name" id="input-1" placeholder="Enter Name" required></b-form-input>
        </b-form-group>
        <b-form-group id="input-group-2" label="Enter Phone Number" label-for="input-2">
          <VueTelInput v-model="form.number" v-on:country-changed="countryChanged" />
        </b-form-group>
        <b-form-group label="Call Now or Later" class="mb-3 pb-1 mt-3">
          <b-form-select v-model="form.send_type" :options="sendType" class="mb-0" required></b-form-select>
        </b-form-group>
        <b-row v-if="form.send_type == 'Scheduled'">
          <b-col md="12">
            <b-form-group label="Call back time">
              <b-form-input type="datetime-local" class="form-control pr-2" v-model="form.call_back_time" id="callbacktime" required></b-form-input>
            </b-form-group>
          </b-col>
          <b-col lg="12" class="mt-2">
            <label>Timezone</label>
            <b-form-select id="input-3" v-model="form.timezone" :options="getTimezones()" required />
          </b-col>
        </b-row>
        <div class="d-flex justify-content-end border-top mt-4 pt-4">
          <b-button type="submit" variant="primary px-5" :disabled="isDisabled">Submit</b-button>
        </div>
        <p :class="errorClass" class="mb-0 text-right mt-2 text-primary">{{ message }}</p>
      </b-form>
    </b-card>
  </div>
</template>

<script>
  import { VueTelInput } from "vue-tel-input";
  import call from "../../js/dialer/call-back";
  import moment from "moment-timezone";
  export default {
    components: { VueTelInput },
    props: ["domain_id", "api_key", "call_flow_id"],
    data() {
      return {
        form: {
          name: "",
          number: "",
          timezone: "",
          api_key: "",
          call_flow_id: "",
          domain_id: "",
          message: "",
          call_back_time: "",
          send_type: "",
          country_code: "",
        },
        isDisabled: false,
        errorClass: "",
        message: "",
        sendType: [
          {
            value: "Call now",
            text: "call now",
          },
          {
            value: "Scheduled",
            text: "call later",
          },
        ],
      };
    },
    async mounted() {
      // To get today's date
      this.form.timezone = moment.tz.guess();
    },
    methods: {
      countryChanged(country) {
        this.form.country_code = country.dialCode;
      },
      getTimezones() {
        let zones = moment.tz.names();
        zones.unshift({ text: "Select timezone", value: "" });
        return zones;
      },
      async onSubmit() {
        this.form.domain_id = this.domain_id;
        this.form.call_flow_id = this.call_flow_id;
        this.form.api_key = this.api_key;
        this.message = "saving..";
        this.isDisabled = false;

        if (this.form.send_type == "Call now") {
          var response = await call.add(this.form);
          this.message = "saved";
        } else {
          response = await call.add(this.form);
          this.message = "saved";
        }
        if (response.Status) {
          if (this.form.send_type == "Call now") this.message = "Call_now_success_helptext";
          else this.message = "Call_later_success_helptext";
          this.errorClass = "text-primary";
        } else if (response.err.response == undefined) {
          this.message = response.err.message;
          this.errorClass = "text-danger";
          this.isDisabled = false;
        }
        setTimeout(() => {
          this.message = "";
        }, 2000);
      },
    },
  };
</script>
