<template>
  <div>
    <div v-if="status == 'new'" class="loading-content">
      <b-spinner variant="primary" label="Spinning"></b-spinner>
    </div>
    <template v-if="status == 'permission_pending'">
      <PermissionMessage />
    </template>
    <template v-if="status == 'ready'">
      <Visitor v-if="from == 'visitor'" :domain_id="domain_id" :flow_id="destination" :api_key="api_key" />
      <Agent v-if="from == 'agent'" ref="agent" app_name="pbxplus" :number="destination" :is_make_call="is_make_call" :is_start_timer="is_start_timer" />
      <Voxdesk v-if="from == 'voxdesk' && enable" :app_name="from" :agent_id="destination" />
    </template>
  </div>
</template>

<script>
  import Permission from "../../js/dialer/permission";
  import Agent from "./Agent.vue";
  import Visitor from "./Visitor.vue";
  import PermissionMessage from "./Permission.vue";
  import Voxdesk from "./Voxdesk";
  import Cookies from "js-cookie";
  import { bus } from "@/js/dialer/emit-bus";
  export default {
    components: { Agent, Visitor, PermissionMessage, Voxdesk },
    data() {
      return {
        status: "new",
        from: "",
        api_key: "",
        destination: 0,
        loadType: "",
        enable: false,
        is_make_call: true,
        is_start_timer: true,
      };
    },
    created() {
      // Event to set jwt for iframe
      window.addEventListener("message", (event) => {
        console.log("message", event);
        if (event.data.loadType == "iframe") {
          this.setCookie(event.data.jwt);
        }
        if (event.data.loadType == "make-call") {
          window.self = this;
          this.destination = event.data.number;
          this.$refs.agent.number = this.destination;
          this.$refs.agent.$refs.keypad.phone_number = this.destination;
          this.$refs.agent.initiateCall();
        }
        // Receive the response from the infinity post
        if (event.data.loadType == "incomming") {
          bus.$emit("incoming", event.data);
        }
      });
      // Check destination
      if (this.$route.params.destination == "number") {
        this.is_start_timer = false;
        this.is_make_call = false;
      }
    },
    mounted: async function() {
      let permission = await Permission.getMedia();
      if (this.$route.params.from == "visitor") {
        this.domain_id = this.$route.query.domain_id;
      }
      this.status = permission.status;
      this.from = this.$route.params.from;
      this.destination = this.$route.params.destination;
      this.api_key = this.$route.params.api_key;
    },
    methods: {
      setCookie(jwt) {
        var jwtObj = { jwt: jwt };
        localStorage.setItem("token", JSON.stringify(jwtObj));
        Cookies.set("token", jwtObj, { secure: true, sameSite: "None" });
        this.enable = true;
      },
    },
    destroyed() {
      localStorage.removeItem("token");
    },
  };
</script>
