<template>
  <div>
    <Agent isLoad="enable" @callConnected="callConnected" :number="agent_id" :app_name="app_name" />
  </div>
</template>
<script>
  import Agent from "./Agent.vue";
  import { bus } from "@/js/core/emit-bus";
  export default {
    components: { Agent },
    props: { agent_id: { type: String }, app_name: { type: String } },
    data() {
      return {
        enable: false,
      };
    },
    mounted() {
      bus.$on("getVoicemailStatus", (callback) => {
        callback(false);
      });
    },
    methods: {
      callConnected() {
        console.log("Call connected");
        parent.postMessage({ isCampaignStart: true }, "*");
      },
    },
  };
</script>
